// 时间戳格式化
export function dateTime(data,type=1){
      if(data==null||data==undefined){
            return ''
      }
      let times=new Date(data)
      let n=times.getFullYear()
      let y=(times.getMonth()+1).toString().padStart(2,0)
      let r=(times.getDate()).toString().padStart(2,0)
      let s=times.getHours()
      let f=times.getMinutes()
      let m=times.getSeconds()
      if(type===1){
            return `${n}-${y}-${r} ${s}:${f}:${m}`
      }else{
            return `${n}-${y}-${r}`
      }
     
}
//数子验证
export const  validatePass = (rule, value, callback) => {
      var reg = new RegExp("^[0-9]*$");
       if(!reg.test(value)){
            callback(new Error('请输入数字'));
        }else if(!/^[0-9]*$/.test(value)){
         callback(new Error('请输入数字!'));
      }else{
            callback()
      }
};

//权限
export const adminFn=(val)=>{
     let arr=[4,5,7]
     return arr.includes(val)
}

